<template>
  <div class="hearderNav">
    <ul>
      <li>
        <a href="javascript:;" @click="searchPape"
          ><span class="icon-search">Search</span></a
        >
      </li>
      <li>
        <router-link to="/"><span class="icon-home">Home</span></router-link>
      </li>
      <li>
        <router-link to="/Catalogue"
          ><span class="icon-books">Catalogue</span></router-link
        >
      </li>
      <li>
        <router-link to="/Link"
          ><span class="icon-link">Link</span></router-link
        >
      </li>
    </ul>
    <div class="searchHover" v-show="dialog"></div>
    <transition name="showDialog" appear>
      <div v-show="dialog" class="search-dialog">
        <div class="search-header">
          <span class="search-dialog-title">文章搜索</span>
          <span class="icon-cross" @click="searchPape"></span>
        </div>
        <div class="search-msg">
          <input type="text" v-model="searchMsg" />
        </div>
        <hr class="hr-2" />
        <div class="search-containar">
          <div
            class="search-content"
            :key="item.id"
            v-for="item in content"
            v-show="searchMsg"
          >
            <div>
              <span class="icon-radio-unchecked"></span
              ><a @click="searchContent(item.id)">{{ item.title }}</a>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "BlogHeaderNav",
  data() {
    return {
      dialog: false,
      // list: [],
      searchMsg: "",
    };
  },
  computed: {
    ...mapState(["content"]),
  },
  watch: {
    searchMsg(newVal, oldVal) {
      if (newVal.length <= 0) {
        this.list = [];
      }
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.search(newVal);
      }, 500);
    },
  },
  methods: {
    searchPape() {
      console.log("被点了");
      this.dialog = !this.dialog;
    },
    searchContent(newid) {
      this.searchPape();
      this.$router.push({
        name: "Content",
        params: {
          id: newid,
        },
      });
      console.log("@" + newid);
    },
    search(value) {
      // console.log(value.length);
      this.$store.dispatch("getMsgArticle", value);
    },
  },
};
</script>
<style lang="css" scope>
ul {
  display: flex;
}
ul li {
  margin-left: 20px;
}
.showDialog-enter-active {
  animation: show 0.5s ease;
}

.showDialog-leave-active {
  animation: show 0.5s ease reverse;
}
@keyframes show {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
.searchHover {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 2000;
}
.search-dialog {
  position: fixed;
  left: 50%;
  top: 100px;
  background-color: #f6f8fa;
  width: 600px;
  margin-left: -300px;
  transform: scale(1);
  z-index: 2001;
  border-radius: 8px;
  padding: 20px;
}
.search-header {
  display: flex;
  justify-content: space-between;
  height: 25px;
  line-height: 25px;
  font-size: 20px;
}
.icon-cross:before {
  height: 25px;
  line-height: 25px;
  cursor: pointer;
}
.search-content {
  cursor: pointer;
}
.search-msg {
  display: flex;
  margin: 0 auto;
  max-width: 100%;
  width: 100%;
  height: 45px;
  line-height: 45px;
  padding: 10px 0px;
}
.search-msg input {
  flex: 1;
  height: 40px;
  border: 2px solid #a4d8fa;
  border-radius: 8px;
  padding: 0 5px;
}

.hr-2 {
  border: 2px dashed #a4d8fa;
  margin: 10px 0;
}

.search-containar {
  max-height: 210px;
  overflow: auto;
}
</style>
