<template>
  <div class="listRightFooter">
    <h3>文章列表</h3>
    <ul>
      <li :key="i.id" v-for="i in lists">
        <img :src="i.imgSrc" alt="" />
        <router-link :to="{
            name: 'Content',
            params: {
              id: i.id,
            },
          }"
          >{{ i.title|titleFilter}} </router-link
        >
      </li>
    </ul>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "BlogListFooter",
  data() {
    return {};
  },
  computed: {
    ...mapState(["lists"]),
  },
//   mounted(){
// console.log(lists);
//   },
  filters: {
    titleFilter(value) {
      return value.substring(9);
    },
  },
};
</script>
<style lang="css" scope>
.listRightFooter {
  width: 260px;
  padding: 20px 24px;
  /* text-align: center; */
}
.listRightFooter:hover{
    box-shadow: 0 3px 8px 6px rgba(7, 17, 27, 0.05);
}
.listRightFooter h3{
    font-size: 18px;
}
.listRightFooter ul {
  display: flex;
  flex-direction: column;
}
.listRightFooter ul img {
  width: 60px;
  height: 60px;
  margin-right: 10px;
}
.listRightFooter ul li {
  margin: 10px;
  display: flex;
  font-size: 14px;
  font-weight: 800;
  /* justify-content: space-around; */
}
</style>
